html {
  scroll-behavior: smooth;
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #711820;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #F3E8E9;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #F3E8E9;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #ffe607;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(126, 2, 195);
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 10;
}
.contenido-video h2 {
  color: #F3E8E9;
  font-size: 7rem;
  margin: 0;
}
.contenido-video p {
  background-color: #711820;
  display: inline-block;
  padding: 1rem 2rem;
  color: #F3E8E9;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.sobre-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 0;
  }
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  color: #ECEAEA;
  font-weight: 700;
}
.contenido-festival p {
  text-align: justify;
}

.lineup {
  background-color: #711820;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #F3E8E9;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}

.escenario {
  padding-top: 2rem;
}
.escenario .nombre-escenario:after {
  content: "";
  display: block;
  width: 100%;
  height: 25rem;
  background-size: 40rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .nombre-escenario:after {
  background-image: url(../img/foto_principal.jpg);
}

.rock .nombre-escenario:after {
  background-image: url(../img/foto_principal.jpg);
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: #C3B3B5;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #a1888b;
}

.bg-verde {
  background-color: #F3E8E9;
}
.bg-verde li:nth-child(odd) {
  background-color: #e2c6c9;
}

.bg-gris {
  background-color: rgba(60, 60, 60, 0.7333333333);
}
.bg-gris li:nth-child(odd) {
  background-color: rgba(35, 35, 35, 0.7333333333);
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #F3E8E9;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #F3E8E9;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: black;
}
.pase .nombre-pase {
  text-align: center;
  font-size: 2.8rem;
  color: #F3E8E9;
}
.pase-header, .pase-footer {
  padding: 0.5rem;
}
.pase-header p, .pase-footer p {
  text-align: center;
  color: #F3E8E9;
  margin: 0;
}
.pase-header {
  background-color: #ECEAEA;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #F3E8E9;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #F3E8E9;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase-footer {
  background-color: #752F97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #C3B3B5;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #000000;
}

